.itemTask{
  border: 2px solid rgb(95, 92, 97);
  border-radius: 5px;
  height: 40px;
  width: 700px;
  color:rgb(87, 12, 28);
  font-size: 22px;
  font-weight: 600;
  line-height: 40px;
  padding: 0 10px;
  position: relative;
  transition: all ease .3s;
}
.za4erk{
  transition: all ease .3s;
  text-decoration: line-through white !important;
  opacity: .7;
}
.wrapper{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  max-width: 720px;
}
.alignCenter{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  max-width: 724px;
  margin: auto;
}
input{
  border: 2px solid rgb(95, 92, 97);
  background: transparent;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  height: 40px;
  width: 70%;
  color: rgb(37, 38, 39);
  font-size: 22px;
  font-weight: 600;
  line-height: 40px;
  padding: 0 10px;
}

button{
  width: 30%;
  height: 44px;
  border-bottom-right-radius: 5px;
  border-top-right-radius: 5px;
  background: transparent;
  border: 2px solid rgb(95, 92, 97);
  cursor: pointer;
}
.close{
  position: absolute;
  right: 20px;
  top: 10px;
  cursor: pointer;
  display: inline-flex;
  width: 20px;
  height: 20px;
  transition: transform ease .5s;
  transform-origin: center;
  transform: rotate(0deg);
}
.redact{
  position: absolute;
  right: 60px;
  top: 10px;
  cursor: pointer;
  display: inline-flex;
  width: 22px;
  height: 20px;
  transition: transform ease .5s;
}
.redact:hover{
  transform: scale(1.5,1.5);
}
.close:hover{
  transform-origin: center;
	transform: rotate(360deg);
}